.form {
    margin-top: 10px;
  }
  
  .field {
    margin-bottom: 10px;
  }
  
  .input,
  .textarea {
    width: 100%;
    padding: 8px;
    border-radius: 4px;
    border: 1px solid #ccc;
  }
  
  .button {
    background-color: #28a745;
    color: white;
    border: none;
    padding: 5px 10px;
    border-radius: 3px;
    cursor: pointer;
    margin-right: 10px;
  }
  
  .button:disabled {
    background-color: #d6d6d6;
    cursor: not-allowed;
  }
  
  .delete {
    background-color: #dc3545;
  }
  
  .edit {
    background-color: #ffc107;
  }
  